import React, { Component, PureComponent } from 'react';
import FlipMove from 'react-flip-move';
import './App.css';
import { move, shuffle, reverse } from './array';

const generateItems = length => [...Array(length).keys()].map((k,i) => ({
  key:i,
  id:`Item ${k}`,
  text:`Item ${k}`,
  opacity:1
}));

const actions = {
  reverse: ({items}) => ({items: reverse(items)}),
  shuffle: ({items}) => ({items: shuffle(items)}),
  move: (oldIndex, newIndex) => ({items}) => ({items: move(items, oldIndex, newIndex)}),
}

const stopEvent = e => {
  console.log('stoppingEvent',e.type, e.target);
  // e.preventDefault();
  // e.stopPropagation();
}
let disabled = false;
let draggingItemId = 'Item 0';
export default class extends Component {

  state = { 
    items: generateItems(16),
  }
  
  move = ({oldIndex, newIndex}) => this.setState(actions.move(oldIndex, newIndex));
  reverse = () => this.setState(actions.reverse);
  shuffle = () => this.setState(actions.shuffle);
  onDragStart = id => {
    console.log('onDragStart', id, this.listRef.style);
    this.listRef.style.touchAction='none';
    draggingItemId = id;
  }
  onDragOver = id => {
    if (disabled) return;
    const oldIndex = this.state.items.findIndex(item => item.id === draggingItemId);
    const newIndex = this.state.items.findIndex(item => item.id === id);
    console.log('onDragOver', id, oldIndex, newIndex);
    this.setState(actions.move(oldIndex, newIndex));
    // draggingItemId = id;
  }
  setListRef = el => {
    this.listRef = el;
    console.log(this.listRef);
  }
  onFlipStart = (childElement, domNode) => {
    console.log('onFlipStart', childElement, domNode)
    disabled=true;
  }
  onFlipEnd = (childElement, domNode) => {
    console.log('onFlipEnd', childElement, domNode)
    disabled=false;
  }
  render() {
    const { items } = this.state;
    return (
      <div className="App"  droppable="true"  onDragOver={this.onDragOver} onDrop={this.onDragOver} >
        {/* <button onClick={this.reverse}>Reverse</button>
        <button onClick={this.shuffle}>Shuffle</button> */}
        <Draggable onDragStart={this.onDragStart} swap={this.swap} onDragOver={this.onDragOver}/>
        <div className="list" ref={this.setListRef}>
          <FlipMove enterAnimation="fade" onStartAll={this.onFlipStart} onFinishAll={this.onFlipEnd}>
            {items.map((item, i) => {
              return <ListItem id={item.id} key={item.key} onDragStart={this.onDragStart}>{item.text}</ListItem>
            })}
          </FlipMove>
        </div>
      </div>
    );
  }
}


class Draggable extends Component {
  state = {id:0}
  onTouchMove = e => {
    console.log("onTouchMove", e.touches);
    const { clientX, clientY } = e.touches[0];
    e.target.style.transform = `translateY(${clientY}px)`;
    const draggingOverElement = document.elementFromPoint(clientX, clientY);
    if (draggingOverElement.id.includes("Item") && draggingItemId !== draggingOverElement.id) {
      console.log(draggingOverElement);
      this.props.onDragOver(draggingOverElement.id);
    }

  }
  render() {
    return <div 
        className="placeholder" 
        draggable="true" 
        onTouchMove={this.onTouchMove}  
        onTouchStart={this.onTouchStart}
        onTouchEnd={this.onTouchEnd}
      />
  }
}

// FlipMove requires class compoonents as children
class ListItem extends Component {
  state = { selected: true };
  onTouchStart = e => {
    this.props.onDragStart(this.props.id);

  }
  render() {
    const { children, id } = this.props;
    return <div 
      onTouchStart={this.onTouchStart}
      className={id==="Item 0" ? 'blank' : 'list-item' }
      id={id}
    > 
      {children}
    </div>
  }
}
